import { forwardRef, PropsWithoutRef, useRef, FC, useEffect, HTMLAttributes } from 'react';
import styled from 'styled-components';

import {
  useConsolidatedRef,
  useEscape,
  useFocusTrap,
  useI18n,
  useOuterEvent,
  useUID
} from '../../hooks';
import { getFocusables } from '../../utils';
import Button from '../Button';
import Text from '../Text';
import Progress from '../Progress';
import { OmitStrict } from '../../types';
import Flex from '../Flex';

import {
  StyledChildrenWrap,
  StyledDialogContent,
  StyledDialogFooter,
  StyledDialogHeader
} from './Dialog.styles';
import { FormDialogProps } from './Dialog.types';
import Dialog from './Dialog';

export const StyledFormDialog = styled(Dialog)``;

const FormDialog: FC<FormDialogProps & OmitStrict<HTMLAttributes<HTMLDivElement>, 'onSubmit'>> =
  forwardRef(
    (
      {
        arrow = true,
        target,
        heading,
        ariaLabel,
        children,
        progress,
        onDismiss,
        onCancel,
        onSubmit,
        ...restProps
      }: PropsWithoutRef<FormDialogProps>,
      ref: FormDialogProps['ref']
    ) => {
      const t = useI18n();
      const headingId = useUID();
      const dialogRef = useConsolidatedRef(ref);
      const cancelButtonRef = useRef<HTMLButtonElement>(null);
      const onCancelHandler = typeof onCancel === 'function' ? onCancel : onCancel.handler;

      useEscape(() => {
        if (onDismiss) {
          onDismiss();
        } else {
          onCancelHandler();
        }
      });

      useOuterEvent('mousedown', [target, dialogRef], () => {
        if (onDismiss) {
          onDismiss();
        } else {
          onCancelHandler();
        }
      });

      useFocusTrap(dialogRef);

      useEffect(() => {
        if (!progress) {
          const focusables = getFocusables(dialogRef);
          if (focusables[0]) focusables[0].focus();
        }
      }, [progress]);

      useEffect(() => {
        return () => {
          if (document.contains(target)) {
            target.focus();
          }
        };
      }, []);

      return (
        <StyledFormDialog
          {...restProps}
          {...(heading === undefined ? { ariaLabel } : { labelledbyId: headingId })}
          arrow={arrow}
          target={target}
          progress={progress}
          ref={dialogRef}
        >
          {heading !== undefined && (
            <Flex
              container={{ alignItems: 'center', justify: 'between', pad: 2 }}
              item={{ shrink: 0 }}
              as={StyledDialogHeader}
            >
              <Text id={headingId} variant='h2'>
                {heading}
              </Text>
            </Flex>
          )}

          <Flex item={{ grow: 1 }} as={StyledDialogContent}>
            {children && <StyledChildrenWrap>{children}</StyledChildrenWrap>}
            <Progress
              visible={!!progress}
              focusOnVisible
              placement={children ? 'local' : 'block'}
              message={typeof progress === 'string' ? progress : undefined}
            />
          </Flex>

          <Flex
            container={{ alignItems: 'center', justify: 'between', pad: 2 }}
            item={{ shrink: 0 }}
            as={StyledDialogFooter}
          >
            <Button
              onClick={() => {
                onCancelHandler();
              }}
              disabled={typeof onCancel !== 'function' ? onCancel.disabled : undefined}
              ref={cancelButtonRef}
            >
              {typeof onCancel !== 'function' && onCancel.text ? onCancel.text : t('cancel')}
            </Button>

            <Button
              variant='primary'
              onClick={() => {
                (typeof onSubmit !== 'function' ? onSubmit.handler : onSubmit)();
              }}
              disabled={typeof onSubmit !== 'function' ? onSubmit.disabled : undefined}
            >
              {typeof onSubmit !== 'function' && onSubmit.text ? onSubmit.text : t('submit')}
            </Button>
          </Flex>
        </StyledFormDialog>
      );
    }
  );

export default FormDialog;
